.header {
  background-color: #FF7438;
  color: #FFF;
  display: -ms-flexbox;
  display: flex;
  font-weight: 100;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1rem;
  text-align: center;
}

.header-primary {
  font-weight: 100;
  font-size: 1.5rem;
  line-height: 40px;
  margin-right: 2rem;
}

.standalone-button {
  margin-bottom: 2rem;
  text-align: center;
}

.table {
  margin: 20px 0;
  padding: 1rem 2rem;
}

.table-item {
  line-height: 35px;
}

.table-header { 
  display: -ms-flexbox; 
  display: flex;
  line-height: 24px;
  font-size: 16px;
  padding: 10px;
}

.table-header-item {
  margin-right: .2rem;
}

.table-header-item:hover {
  cursor: pointer;
}
  
.table-header-item-active {
  text-decoration: underline;
}

.table-empty {
  margin: 200px;
  text-align: center;
  font-size: 16px;
}

.button-search {
  border-radius: 0;
  color: #fff;
  font-size: 1rem;
  height: 100%;
}

.button-load {
  border: 1px solid #000;
}

.button {
  border: 1px solid inherit;
  background: #fff;
  color: inherit;
  text-align: center;
  -webkit-font-smoothing: inherit;
  padding: .5rem;
  font-size: inherit;
  cursor: pointer;
}

.button-dismiss:hover {
  color: red;
}

.button-search:hover {
  color: #fff;
}

.button-active {
  border-radius: 0;
  border-bottom: 1px solid #38BB6C;
}

.table-row {
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
  white-space: nowrap;
  padding: 10px;
  background: #F6F6F0;
  border: 1px solid #e3e3e3;
}

.table-header > span {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.table-row > span {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}




